.room-code {
  height: 40px;
  border-radius: 8px;
  overflow: hidden;
  
  background: rgb(41, 41, 41);
  color: white;
  border: 1px solid #318d5c;
  cursor: pointer;
  
  display: flex;

  div {
    background-image: linear-gradient(to right, #43e97b 0%, #38f9d7 100%);
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  span {
    display: block;
    align-self: center;
    flex: 1;
    padding: 0 16px 0 12px;
    width: 230px;
    font-size: 14px;
    font-weight: 500;
  }
}
